* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  background-image: linear-gradient(to right, rgba(28, 117, 250, .4), rgba(238, 45, 11, .4));
  padding: 50px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  border: 2px solid #409fec;
  border-radius: 4px;
  margin-bottom: 16px;
  padding: 8px;
  transition: border, box-shadow .3s;
  display: block;
}

#search-box:focus {
  border: 2px solid #d67592;
  outline: none;
  box-shadow: 0 0 20px #87ceeb;
}

.country-info p {
  margin: 0 0 8px;
  font-size: 24px;
}

.country-info p span {
  color: #00f;
  font-weight: 500;
}

.country-info p:first-child span {
  color: #000;
  text-transform: uppercase;
  font-weight: 700;
}

.country-list {
  padding: 0;
  font-size: 20px;
  list-style: none;
}

.country-list li {
  gap: 20px;
  display: flex;
}

.country-list li:not(:last-child) {
  margin-bottom: 10px;
}

